import './EventChanger.css';
import React, { useState, useEffect } from 'react';

function EventChanger() {
  const testUrl = 'http://localhost:5000/rooms';
  const prodUrl = 'https://it-care-finish.herokuapp.com/rooms';

  let daten = fetchRooms();

  // Fetch rooms
  async function fetchRooms() {
    const res = await fetch(prodUrl);
    const data = await res.json();
    return data;
  }

  async function fetchRoom() {
    let choice = document.getElementById('id-choice');
    let valueId = choice.value;
    const fetchRoom = await fetch(`${prodUrl}/${valueId}`);
    const room = await fetchRoom.json();
    return room;
  }

  function handleClick(e) {
    return fetchRooms().then((data) => {
      var select = document.getElementById('id-choice');
      while (select.firstChild) {
        select.removeChild(select.firstChild);
      }
      for (let roomNr in data) {
        var option = document.createElement('option');
        option.text = data[roomNr].name + ' - ' + data[roomNr].owner.name;
        option.value = data[roomNr].id;
        select.appendChild(option);
      }
    });
  }

  function toEdit() {
    var sensorList = document.getElementById('sensorName');
    while (sensorList.firstChild) {
      sensorList.removeChild(sensorList.firstChild);
    }
    var eventList = document.getElementById('eventSelect');
    while (eventList.firstChild) {
      eventList.removeChild(eventList.firstChild);
    }
    let room = fetchRoom().then((data) => {
      let sensorArray = data.deviceList;
      for (let sensor in sensorArray) {
        var optionName = document.createElement('option');
        optionName.text = sensorArray[sensor].name;
        sensorList.appendChild(optionName);
      }
      for (let i = 0; i < 7; i++) {
        var optionEvent = document.createElement('option');
        if (i !== sensorArray[0].event) {
          optionEvent.text = i;
          optionEvent.value = i;
          eventList.appendChild(optionEvent);
        }
      }
      optionEvent.text = sensorArray[0].event;
      optionEvent.value = sensorArray[0].event;
      optionEvent.style.color = 'red';
      optionEvent.style.backgroundColor = 'yellow';
      eventList.appendChild(optionEvent);
    });
  }

  function fetchEvent() {
    var sensorList = document.getElementById('sensorName');
    let sensorText = sensorList.value;
    console.log(sensorText);
    var eventList = document.getElementById('eventSelect');
    while (eventList.firstChild) {
      eventList.removeChild(eventList.firstChild);
    }
    let room = fetchRoom().then((data) => {
      let sensorArray = data.deviceList;
      let sensorPosition;
      for (let i = 0; i < sensorArray.length; i++) {
        if (sensorText === sensorArray[i].name) {
          sensorPosition = i;
          console.log(sensorPosition);
          break;
        }
      }

      for (let i = 0; i < 7; i++) {
        var optionEvent = document.createElement('option');
        if (i !== sensorArray[sensorPosition].event) {
          optionEvent.text = i;
          optionEvent.value = i;
          eventList.appendChild(optionEvent);
        }
      }
      optionEvent.text = sensorArray[sensorPosition].event;
      optionEvent.value = sensorArray[sensorPosition].event;
      optionEvent.style.color = 'red';
      optionEvent.style.backgroundColor = 'yellow';
      eventList.appendChild(optionEvent);
    });
  }

  async function update() {
    let choice = document.getElementById('id-choice');
    let valueId = choice.value;
    var eventList = document.getElementById('eventSelect');
    let eventValue = eventList.value
    console.log("update")
    const fetchRoom = await fetch(`${prodUrl}/${valueId}`);
    const room = await fetchRoom.json();
    var sensorName = document.getElementById('sensorName');
    console.log(sensorName.value)
    for(let device in room.deviceList){
      if(room.deviceList[device].name===sensorName.value){
       room.deviceList[device].event = parseInt(eventValue)
      }
    }
  
    const requestOptions = {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(room)
  };
  fetch(`${prodUrl}/${valueId}`, requestOptions)
      .then(response => response.json())
      .then();
 fetchEvent();
  }

  /*
  for (let room in data) {
        var option = document.createElement("option");
        option.text = room.id
        option.value = "myvalue";
        var select = document.getElementById('id-choice');
        select.appendChild(option);
      }

  */

  return (
    <div>
      <label>
        <h3>Wohnung</h3>
        <select id='id-choice' size='1'>
          <option id='to-delete'>Auf suchen klicken</option>
        </select>
      </label>
      <button onClick={handleClick}>Suchen</button>
      <button onClick={toEdit}>Bearbeiten</button>
      <form name='Testform' >
        <div>
          <h3>Sensoren</h3>
          <div>
            <div>
              <label>
                Name:
                <select onChange={fetchEvent} id='sensorName' size='1'></select>
              </label>
              {/* <label>
                Nutzung:
                <select name='top5' size='1'>
                  <option></option>
                  <option> </option>
                </select>
              </label> */}
              <label>
                Ereignis:
                <select id='eventSelect' size='1'></select>
              </label>
            </div>
          </div>
        </div>

        {/* <div>
          <h3>Türen</h3>
          <div>
            <div>
              <label>
                Name:
                <select name='top5' size='1'>
                  <option></option>
                  <option> </option>
                </select>
              </label>
            </div>
          </div>
          <div>
            <div>
              <label>
                Offen:
                <select name='top5' size='1'>
                  <option></option>
                  <option> </option>
                </select>
              </label>
            </div>
          </div>
        </div>
        <div>
          <h3>Bewohner</h3>
          <div>
            <div>
              <label>
                Name:
                <select name='top5' size='1'>
                  <option></option>
                  <option> </option>
                </select>
              </label>
            </div>
          </div>
          <div>
            <div>
              <label>
                Termine:
                <select name='top5' size='1'>
                  <option></option>
                  <option> </option>
                </select>
              </label>
            </div>
          </div>
        </div>         */}
      </form>
      <button onClick={update}>Speichern</button>
      
    </div>
  );
}
export default EventChanger;
