import { Link } from 'react-router-dom';
import './../App.css';
import Logo from './img/logo.svg';
import Clock from 'react-digital-clock';
import React from 'react';

function Nav() {
  const navStyle = {
    color: 'white',    
  };
  return (
    <div>    
      <nav className='nav'>
      <img className='App-logo' src={Logo} />
        <h3 className='logo-titel'>It-Care-App</h3>
        <ul className='nav-links'>
        <Link style={navStyle} to='/'>
            <li>Home</li>
          </Link>
            <Link style={navStyle} to='/stage'>
            <li>Demo</li>
          </Link>
          {/*
           <Link style={navStyle} to='/news'>
            <li>Meldungen</li>
          </Link>         
          <Link style={navStyle} to='/termin'>
            <li>Termine</li>
          </Link>
          */}
          <Link style={navStyle} to='/event'>
            <li>EC</li>
          </Link>
          <Link style={navStyle} to='/mygekko'>
            <li>Online</li>
          </Link>
          <Clock hour12={false}/>
        </ul>
      </nav>
    </div>
  );
}
export default Nav;