import React from 'react';
import RoomImage from './img/room.svg';
import { FaWheelchair, FaShower, FaBed, FaInfo } from 'react-icons/fa';
import { IoIosMan } from 'react-icons/io';
import { GiCookingPot, GiHidden } from 'react-icons/gi';
import './../App.css';


const Room = ({ roomName, deviceList, gateList, owner }) => {
  let sensorOne = deviceList[3];
  let sensorTwo = deviceList[4];
  let shower = deviceList[1];
  let bed = deviceList[0];
  let cooker = deviceList[2];

  let door1 = gateList[0];
  let door2 = gateList[1];
  let door3 = gateList[2];
  let door4 = gateList[3];

  function eventInterpret(device) {
    if (device.event === 1) {
      return 'F3E233';
    }
    if (device.event === 2) {
      return '6FD935';
    }
    if (device.event === 3) {
      return 'AC51E5';
    }
    if (device.event === 4) {
      return 'orange';
    }
    if (device.event === 5) {
      return 'red';
    }
    if (device.event === 6) {
      return 'blue';
    }
    return 'gray';
  }

  function visibleInfo() {
    let property = roomName + 'infoView';
    document.querySelector('[name="'+property+'"]').style.visibility = 'visible';
  }

  function hiddenInfo() {
    let property = roomName + 'infoView';
    document.querySelector('[name="'+property+'"]').style.visibility = 'hidden';
  }

  return (
    <div>
      <h3>{roomName + ' - ' + owner.name}</h3>
      {/*       <ol>
        {deviceList.map((device) => (
          <li>
            {device.name} {device.use ? 'wird benutzt' : 'wird nicht benutzt'}
          </li>
        ))}
      </ol>
      <ol>
        {gateList.map((gate) => (
          <li>
            {gate.name} {gate.open ? 'ist geöffnet' : 'ist geschlossen'}
          </li>
        ))}
      </ol>
      <p>{owner.name}</p>
      <ol>
        {owner.meetings.map((meeting) => (
          <li>{meeting}</li>
        ))}
      </ol> */}
      {/* <div>
        <img src={RoomImage} />
      </div> */}
      {/* <div>
        <FaWheelchair class='top' size='5em' color='orange' />
        <FaWheelchair size='5em' color='yellow' />
      </div> */}

      <div className='parent'>
        <img className='image1' src={RoomImage} />

        <FaShower
          className='image3'
          size='2.5em'
          color={eventInterpret(shower)}
        />
        <FaBed className='image4' size='6em' color={eventInterpret(bed)} />
        <GiCookingPot
          className='image5'
          size='3em'
          color={eventInterpret(cooker)}
        />
        <IoIosMan
          className='image6'
          size='6em'
          color={eventInterpret(sensorTwo)}
          style={
            sensorTwo.wheelchair
              ? { visibility: 'hidden' }
              : { visibility: 'visible' }
          }
        />
        <FaWheelchair
          className='image6'
          size='4em'
          color={eventInterpret(sensorTwo)}
          style={
            sensorTwo.wheelchair
              ? { visibility: 'visible' }
              : { visibility: 'hidden' }
          }
        />

        <FaInfo
          onMouseOver={visibleInfo}
          onMouseOut={hiddenInfo}
          className='image7'
          size='2em'
          color={'black'}
        />
        <div id='infoView' name={roomName + 'infoView'}>
          <p>
          Temperatur: 20°C<br/>        
          Luftfeuchtigkeit: 54%<br/>          
          CO2: 780 ppm<br/>
          Licht: 450 Lux<br/>
          </p>
        </div>
        <div
          key={'test'}
          id='door1'
          style={
            door1.open
              ? { backgroundColor: 'green' }
              : { backgroundColor: 'red' }
          }
        ></div>
      </div>
    </div>
  );
};
export default Room;
