import './App.css';
import { useState, useEffect } from 'react';
import Room from './components/Room';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Nav from './components/Nav';
import Stage from './components/Stage';
import News from './components/News';
import Termin from './components/Stage';
import RoomPage from './components/RoomPage';
import Logo from './components/img/HomePageLogo.svg';
import HomePage from './components/HomePage';
import EventChanger from './components/EventChanger';
import MyGekkoData from './components/MyGekkoData';

function App() {
  return (
    <Router>
      <div className='App'>
        <Nav />
        <body className='App-body'>
          <Switch>
            <Route path='/' exact component={HomePage} />
            <Route path='/stage' exact component={Stage} />
            {/* <Route path='/news' component={News} />
            <Route path='/termin' component={Termin} /> */}
            <Route path='/stage/:id' component={RoomPage} />
            <Route path='/news' component={News} />
            <Route path='/event' component={EventChanger} />
            <Route path='/mygekko' component={MyGekkoData} />
          </Switch>
        </body>
        {/* <footer className='App-footer'>
          <p></p>
        </footer> */}
      </div>
    </Router>
  );
}

const Home = () => (
  <div className='App'>
    <div className='App-body'>
      <div className='container'>
        <div className='columnRoom'>
          <img src={Logo} alt='' />
        </div>
        <div className='columnText'>
          <h1>Digitale Pflege</h1>
          <p>
            Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam
            nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam
            erat, sed diam voluptua. At vero eos et accusam et justo duo dolores
            et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est
            Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur
            sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore
            et dolore magna aliquyam erat, sed diam voluptua. At vero eos et
            accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren,
            no sea takimata sanctus est Lorem ipsum dolor sit amet.
          </p>
        </div>
      </div>
    </div>
  </div>
);

export default App;
