import './../App.css';
import React, { useState, useEffect } from 'react';
import Room from './Room';

function RoomPage({ match }) {
  // useEffect(() => {
  //   fetchRoom();
  // }, []);

  // const [room, setRoom] = useState({
  //   owner: {},
  //   deviceList: {},
  // });

  // const fetchRoom = async () => {
  //   const fetchRoom = await fetch(
  //     `http://localhost:5000/rooms/${match.params.id}`
  //   );
  //   const room = await fetchRoom.json();
  //   return room;
  // };

  // return (
  //   <div>
  //     <div></div>
  //   </div>
  // );

  const navStyle = {
    color: 'white',
  };
  const testUrl = 'http://localhost:5000/rooms';
  const prodUrl = 'https://it-care-finish.herokuapp.com/rooms';
  const [rooms, setRooms] = useState([]);

  useEffect(() => {
    const getRooms = async () => {
      const roomsFromServer = await fetchRooms();
      setRooms(roomsFromServer);
    };
    getRooms();
  });
  
  // Fetch PUT room

  // Fetch rooms
  const fetchRooms = async () => {
    const res = await fetch(prodUrl);
    const data = await res.json();

    //console.log(data.id === `${match.params.id}`);
    return data;
  };
  return (
    <div className='App'>
      <body className='App-body'>
        <div className='container'>
          <div className='columnRoom'>
            {rooms
              .filter((r) => r.id === `${match.params.id}`)
              .map((room) => (
                <Room
                  roomName={room.name}
                  deviceList={room.deviceList}
                  gateList={room.gateList}
                  owner={room.owner}
                />
              ))}
          </div>
          <div className='columnText'>
            {rooms
              .filter((r) => r.id === `${match.params.id}`)
              .map((room) => (
                <div>
                  <h1>Zur Person</h1>
                  <p>{room.owner.name}</p>
                  <p>Alter:</p>
                  <p>Pflegestufe:</p>
                  <p>Körpergewicht:</p>
                  <p>Blutdruck: </p>
                  <p>Anwesenheit: </p>
                  <h1>Termine</h1>
                  <p>{room.owner.meetings[0]}</p>
                  <p>{room.owner.meetings[1]}</p>
                  <p>{room.owner.meetings[2]}</p>
                  <h1>Angehörige</h1>
                  <p>Verwandschaftsverhältnis: </p>
                  <p>Name: </p>
                  <p>Vorname: </p>
                  <p>Telefonnummer: </p>
                </div>
              ))}
          </div>
        </div>
      </body>
    </div>
  );
}

export default RoomPage;
