import './../App.css';
import { useState, useEffect } from 'react';
import Room from './Room';
import { Link } from 'react-router-dom';

function Stage() {
  const navStyle = {
    color: 'white',    
  };
  const testUrl = 'http://localhost:5000/rooms';
  const prodUrl = 'https://it-care-finish.herokuapp.com/rooms';
  const [rooms, setRooms] = useState([]);

  useEffect(() => {
    const getRooms = async () => {
      const roomsFromServer = await fetchRooms();
      setRooms(roomsFromServer);
    };
    getRooms();
  });

  
  // Fetch rooms
  const fetchRooms = async () => {
    const res = await fetch(prodUrl);
    const data = await res.json();
    return data;
  };
  return (
      <div className='App'>
        <body className='App-body'>
          <div className='rooms'>
            {rooms.map((room) => (
              <Link style={navStyle} to={`/stage/${room.id}`}>
              <Room
                roomName={room.name}
                deviceList={room.deviceList}
                gateList={room.gateList}
                owner={room.owner}
              />
              </Link>
            ))}
          </div>
        </body>
      </div>
  );
}

export default Stage;
