import './EventChanger.css';
import React, { useState, useEffect } from 'react';
import Events from './Events';

function CO2() {
  const CO2url = 'https://it-care.foyang-tech.de/api/v1/patient/careevent/L96N-WOPZ-8YPE-KFI3';
  const [CO2s, setCO2s] = useState([]);

  useEffect(() => {
    const getCO2 = async () => {
      const co2FromServer = await fetchCO2();
      setCO2s(co2FromServer);
    };
    getCO2();
  });

  const fetchCO2 = async () => {
    const res = await fetch(CO2url);
    const data = await res.json();
    return data;
  };
  
  var gekkoId;
  let age;
  let name;
  let firstname;
  let lastname;
  let careLevel;
  let co2_id;
  let co2_type;
  let co2_update
 
  return (
    <div> 
    <h3 className="gekkoId">{CO2s.gekkoId}</h3> 
    <h4 className="age">{CO2s.age}</h4> 
    <h4 className="firstname">{CO2s.firstname}</h4> 
    <h4 className="lastname">{CO2s.lastname}</h4> 
    <h4 className="carelevel">{CO2s.careLevel}</h4> 
    <Events 
        roomName={CO2s}
    />
    </div> 
  );
  
}
export default CO2;
