import React from 'react';
import RoomImage from './img/room.svg';
import './../App.css';
import { useState, useEffect } from 'react';


const RoomMyGekko = () => {
  const eventURL = 'https://it-care.foyang-tech.de/api/v1/patient/careevent/L96N-WOPZ-8YPE-KFI3';
  const [Events, setEvents] = useState([]);

  useEffect(() => {
    const getEvents = async () => {
      const eventsFromServer = await fetchEvents();
      setEvents(eventsFromServer);
    };
    getEvents();
  });

  const fetchEvents = async () => {
    const res = await fetch(eventURL);
    const data = await res.json();
    return data;
  };
  
 
/*   return (
    <div> 
    <h3 className="gekkoId">{CO2s.gekkoId}</h3> 
    <h4 className="age">{CO2s.age}</h4> 
    <h4 className="firstname">{CO2s.firstname}</h4> 
    <h4 className="lastname">{CO2s.lastname}</h4> 
    <h4 className="carelevel">{CO2s.careLevel}</h4> 
    <Events 
        roomName={CO2s}
    />
    </div> 
  ); */

  let idCO2 //= roomName.events[0].id
  let nameCO2 //= roomName.events[0].name
  let typeCO2 //= roomName.events[0].type
  let updateCO2 //= roomName.events[0].updated
  let colorCO2
  let valueCO2

  if(Events.events === undefined){
    console.log("fetch data") 
  }else{
    idCO2 = Events.events[2].id
    nameCO2 = Events.events[2].name
    typeCO2 = Events.events[2].type.substring(0,3)
    updateCO2= Events.events[2].updated
    colorCO2 = Events.events[2].color    
    valueCO2 = Events.events[2].value
  }

  let idTemp //= roomName.events[0].id
  let nameTemp //= roomName.events[0].name
  let typeTemp //= roomName.events[0].type
  let updateTemp //= roomName.events[0].updated
  let colorTemp
  let valueTemp

  if(Events.events === undefined){
    console.log("fetch data") 
  }else{
    idTemp = Events.events[0].id
    nameTemp = Events.events[0].name
    typeTemp = Events.events[0].type.substring(0,3)
    updateTemp= Events.events[0].updated
    colorTemp = Events.events[0].color   
    valueTemp = Events.events[0].value 
  }

  let idLight //= roomName.events[0].id
  let nameLight //= roomName.events[0].name
  let typeLight //= roomName.events[0].type
  let updateLight //= roomName.events[0].updated
  let colorLight
  let valueLight

  if(Events.events === undefined){
    console.log("fetch data") 
  }else{
    idLight = Events.events[1].id
    nameLight = Events.events[1].name
    typeLight = Events.events[1].type.substring(0,4)
    updateLight= Events.events[1].updated
    colorLight = Events.events[1].color   
    valueLight = Events.events[1].value 
  }

  let idHumi //= roomName.events[0].id
  let nameHumi //= roomName.events[0].name
  let typeHumi //= roomName.events[0].type
  let updateHumi //= roomName.events[0].updated
  let colorHumi
  let valueHumi

  if(Events.events === undefined){
    console.log("fetch data") 
  }else{
    idHumi = Events.events[3].id
    nameHumi = Events.events[3].name
    typeHumi = Events.events[3].type.substring(0,3)
    updateHumi= Events.events[3].updated
    colorHumi = Events.events[3].color
    valueHumi = Events.events[3].value    
  }

  function changeColorCO2(){
    if(colorCO2==="BLUE"){
      document.querySelector("#CO2div").style.backgroundColor='blue'  
    }
    if(colorCO2==="RED"){
      document.querySelector("#CO2div").style.backgroundColor='red'  
    }else{
    }
  }

  function changeColorTemp(){
    if(colorTemp==="YELLOW"){
      document.querySelector("#Tempdiv").style.backgroundColor='yellow'  
    }
    if(colorTemp==="RED"){
      document.querySelector("#Tempdiv").style.backgroundColor='red'  
    }else{
    }
  }

  function changeColorLight(){
    if(colorLight==="GREEN"){
      document.querySelector("#LightDiv").style.backgroundColor='GREEN'  
    }
    if(colorLight==="RED"){
      document.querySelector("#LightDiv").style.backgroundColor='red'  
    }else{
    }
  }

  function changeColorHumidity(){
    if(colorHumi==="GREEN"){
      document.querySelector("#HumiDiv").style.backgroundColor='GREEN'  
    }
    if(colorHumi==="RED"){
      document.querySelector("#HumiDiv").style.backgroundColor='red'  
    }else{
    }
  }
  
  changeColorHumidity()
  changeColorLight()
  changeColorTemp()
  changeColorCO2()

  function visibleInfoHUM() {
    let property = 'infoViewHUM';
    document.querySelector('[name="'+property+'"]').style.visibility = 'visible';
  }

  function hiddenInfoHUM() {
    let property = 'infoViewHUM';
    document.querySelector('[name="'+property+'"]').style.visibility = 'hidden';
  }

  function visibleInfoLIGHT() {
    let property = 'infoViewLIGHT';
    document.querySelector('[name="'+property+'"]').style.visibility = 'visible';
  }

  function hiddenInfoLIGHT() {
    let property = 'infoViewLIGHT';
    document.querySelector('[name="'+property+'"]').style.visibility = 'hidden';
  }

  function visibleInfoTEMP() {
    let property = 'infoViewTEMP';
    document.querySelector('[name="'+property+'"]').style.visibility = 'visible';
  }

  function hiddenInfoTEMP() {
    let property = 'infoViewTEMP';
    document.querySelector('[name="'+property+'"]').style.visibility = 'hidden';
  }

  
  function visibleInfoCO2() {
    let property = 'infoViewCO2';
    document.querySelector('[name="'+property+'"]').style.visibility = 'visible';
  }

  function hiddenInfoCO2() {
    let property = 'infoViewCO2';
    document.querySelector('[name="'+property+'"]').style.visibility = 'hidden';
  }


  return (
    <div>
    <div>
          <p>{Events.firstname+" "+Events.lastname+" - "+Events.gekkoId}</p>
    </div>
    <div className='parent'>
    <img className='image1' src={RoomImage} /> 

    <div id="CO2div"  className="infoView"
      onMouseOver={visibleInfoCO2}
      onMouseOut={hiddenInfoCO2}  

    style={{      
      width: '2.2em',
        height: '1.2em',
        position: "absolute",
        top: "720px",
        left: "12px",
        borderRadius: "5px",
        color:"black"
      }}>{typeCO2}
      </div>
      <div id='infoViewCO2' name={'infoViewCO2'}>
          <p>
          {"CO2"}<br/>        
          {valueCO2}<br/>         
          {"Stand:"}<br/>
          {updateLight}<br/>
          </p>
        </div>      


      <div id="Tempdiv"  className="infoView"     
      onMouseOver={visibleInfoTEMP}
      onMouseOut={hiddenInfoTEMP}
    style={{      
      width: '2.2em',
        height: '1.2em',
        position: "absolute",
        top: "690px",
        left: "12px",
        borderRadius: "5px",
        color:"black"
      }}>{typeTemp}
      </div>
      <div id='infoViewTEMP' name={'infoViewTEMP'}>
          <p>
          {"Temperatur"}<br/>        
          {valueTemp}<br/>         
          {"Stand:"}<br/>
          {updateLight}<br/>
          </p>
        </div>
      

      <div id="LightDiv"  className="infoView"    
      onMouseOver={visibleInfoLIGHT}
      onMouseOut={hiddenInfoLIGHT} 
    style={{      
      width: '2.2em',
        height: '1.2em',
        position: "absolute",
        top: "660px",
        left: "12px",
        borderRadius: "5px",
        color:"black"
      }}>{typeLight}
      </div>

      <div id='infoViewLIGHT' name={'infoViewLIGHT'}>
          <p>
          {"Licht"}<br/>        
          {valueLight}<br/>         
          {"Stand:"}<br/>
          {updateLight}<br/>
          </p>
        </div>

      <div id="HumiDiv"  className="infoView"  
        onMouseOver={visibleInfoHUM}
        onMouseOut={hiddenInfoHUM}   
    style={{      
        width: '2.2em',
        height: '1.2em',
        position: "absolute",
        top: "630px",
        left: "12px",
        borderRadius: "5px",
        color:"black"
      }}>{typeHumi}
      </div>
      <div id='infoViewHUM' name={'infoViewHUM'}>
          <p>
          {"Feuchtigkeit"}<br/>        
          {valueHumi}<br/>         
          {"Stand:"}<br/>
          {updateHumi}<br/>
          </p>
        </div>
        <div
        ></div>                 
    </div>
    </div>    
  );
};
export default RoomMyGekko;
